* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
    font-weight:bold;
  }

body {
  background:white;
}



.todo-app {
  display: flex;
  flex-direction: column;
  justify-content: start;
  width: 520px;
  min-height: 600px;
  background: #ffffff;
  border:black solid 3px;
  text-align: center;
  margin: 128px auto;
  border-radius: 10px;
  padding-bottom: 32px;
}

h1 {
  margin: 32px 0;
  color: black;
  font-size: 24px;
}

.complete {
  text-decoration: line-through;
  opacity: 0.4;
}

.todo-form {
  margin-bottom: 32px;
}

.todo-input {
  padding: 14px 32px 14px 16px;
  border-radius: 4px 0 0 4px;
  border: 2px solid #000000;
  outline: none;
  width: 320px;
  background:rgba(192, 192, 192, 0.849);
  color:black;
}

.todo-input::placeholder {
  color:rgb(12, 12, 12);
}

.todo-button {
  padding: 16px;
  border: none;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
  outline: none;
  background: black;
  color: white;
  text-transform: capitalize;
}

.todo-input.edit {
  border: 2px solid black;
}

.todo-button.edit {
  background:black;
  padding: 16px 22px;
}

.todo-container {
  display: flex;
  flex-direction: row;
  position: relative;
}

.todo-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 4px auto;
  color: black;
  background: rgba(192, 192, 192, 0.849);
  border: black solid 3px;
  padding: 16px;
  border-radius: 5px;
  width: 90%;
}

.todo-row:nth-child(4n + 1) {
  background:rgba(192, 192, 192, 0.849);
}

.todo-row:nth-child(4n + 2) {
  background:rgba(192, 192, 192, 0.849);
}

.todo-row:nth-child(4n + 3) {
  background:rgba(192, 192, 192, 0.849);
}

.icons {
  display: flex;
  align-items: center;
  font-size: 24px;
  cursor: pointer;
}

.delete-icon {
  margin-right: 5px;
  color: black;
}

.edit-icon {
  color: black;
}
